const moreTagsElement = document.getElementById('more-tags');
const tagItems = document.querySelectorAll('.user-form__tag-item');

moreTagsElement.addEventListener('click', (event) => {
  event.preventDefault();
  tagItems.forEach((item) => {
    item.classList.remove('user-form__tag-item--hidden');
  });
});


let isListOpen = false;

const classElement = document.getElementById('class');
const selectList = document.querySelector('.user-form__select-list');
const selectItems = document.querySelectorAll('.user-form__select-item');

classElement.addEventListener('click', () => {
  if (isListOpen) {
    selectList.classList.add('user-form__select-list--hidden');
  } else {
    selectList.classList.remove('user-form__select-list--hidden');
  }
  isListOpen = !isListOpen;
});

selectItems.forEach((item) => {
  item.addEventListener('click', () => {
    const value = item.getAttribute('data-value');
    const input = classElement.querySelector('input');
    input.value = value;

    selectItems.forEach((i) => i.classList.remove('user-form__select-item--checked'));

    item.classList.add('user-form__select-item--checked');

    selectList.classList.add('user-form__select-list--hidden');
    isListOpen = false;
  });
});

document.addEventListener('click', (event) => {
  if (!classElement.contains(event.target) && event.target !== classElement) {
    selectList.classList.add('user-form__select-list--hidden');
    isListOpen = false;
  }
});


const inputElement = document.querySelector('.user-form__input');
const inputIconElement = document.querySelector('.user-form__input-icon');

inputElement.addEventListener('input', () => {
  if (inputElement.value.trim() !== '') {
    inputIconElement.classList.remove('user-form__input-icon--hidden');
  } else {
    inputIconElement.classList.add('user-form__input-icon--hidden');
  }
});

inputIconElement.addEventListener('click', () => {
  inputElement.value = '';
  inputIconElement.classList.add('user-form__input-icon--hidden');
});


const textareaElement = document.querySelector('.user-form__textarea');
const counterElement = document.querySelector('.user-form__textarea-counter');
const messageElement = document.querySelector('.user-form__textarea-message');
const textareaIdElement = document.querySelector('#textarea');

const limit = 200;

textareaElement.addEventListener('input', () => {
  const textLength = textareaElement.value.length;
  counterElement.textContent = `${textLength} / ${limit}`;

  if (textLength > limit) {
    textareaIdElement.classList.add('user-form__label--error');
    messageElement.classList.remove('user-form__textarea-message--hidden');
    counterElement.classList.add('user-form__textarea-counter--error');
  } else {
    textareaIdElement.classList.remove('user-form__label--error');
    messageElement.classList.add('user-form__textarea-message--hidden');
    counterElement.classList.remove('user-form__textarea-counter--error');
  }
});


const nextButton = document.querySelector('.user-form__control-button--next');
const inputElements = document.querySelectorAll('.input.user-form__input');

function checkInputs() {
  let isValid = true;

  inputElements.forEach((input) => {
    if (input.value.trim() === '') {
      isValid = false;
    }
  });

  if (textareaElement.value.trim() === '') {
    isValid = false;
  }

  if (isValid) {
    nextButton.removeAttribute('disabled');
  } else {
    nextButton.setAttribute('disabled', 'disabled');
  }
}

inputElements.forEach((input) => {
  input.addEventListener('input', checkInputs);
});

textareaElement.addEventListener('input', checkInputs);

checkInputs();
